
.navbar-container {
  background-color: #333; 
  padding: 10px; 
  display: flex; 
  justify-content: space-around;
}

/* Navbar Link Styles */
.navbar-container a {
  text-decoration: none; 
  color: white; 
  font-weight: bold; 
  font-size: 16px; 
}


.navbar-container a:hover {
  color: lightblue; 
}
